.about {
  padding-top: 8rem;
  background-image: url(../img/bg--1.svg);
  background-size: cover;
  background-position: bottom;
  background-attachment: fixed;

  &__inner {
    // display: flex;

    &-left {
      display: grid;
      grid-gap: 3rem;
      justify-items: start;

      & > :nth-child(2) {
        display: grid;
        justify-self: center;
      }
    }
    &-right {
      display: grid;
      grid-gap: 1.5rem;

      &__heading {
      }
      &__text {
      }
    }
  }
}
